import {BehaviorSubject} from 'rxjs';


export class UsersService {

  contentListing: any;
  listing: any;

  constructor() {

    this.contentListing = [{
      user: 'Gagan',
      experience: 32
    }, {
      user: 'Abhinav',
      experience: 32
    }, {
      user: 'Abhishek',
      experience: 32
    }, {
      user: 'Neha',
      experience: 32
    }];
    this.listing = new BehaviorSubject(this.contentListing);
  }

  getUsers() {
    return this.listing;
  }

  updateData() {
    setTimeout(() => {
      this.listing.next([{
        user: 'Gagan',
        experience: 32
      }]);
      console.log('cleaned data');
    }, 2000);
  }

  changeName(name) {
    this.listing.next([{
      user: name,
      experience: 1
    }]);
  }
}
